import Vue from 'vue';
import App from './App.vue';
import '@fortawesome/fontawesome-free/css/all.css';
// Configuracin de la URL de la API
const apiUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost:8000/api' 
  : 'https://apicandidatos.talentsafecontrol.com/api';


  // Configuracin de la URL de las imgenes
  const imageUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost/rodi_portal/_perfilEmpleado/'  // Asegrate de que la ruta sea correcta
  : 'https://portal.talentsafecontrol.com/_perfilEmpleado/';

  const documentUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost/rodi_portal/_documentEmpleado/'  // Asegrate de que la ruta sea correcta
  : 'https://portal.talentsafecontrol.com/_documentEmpleado/';

  // Carga el token CSRF en Vue
const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;


// Hacer que la URL de la API est disponible globalmente
Vue.prototype.$apiUrl = apiUrl;
Vue.prototype.$imageUrl = imageUrl;
Vue.prototype.$documentUrl = documentUrl;
Vue.prototype.$csrfToken = csrfToken;
/*
/* */
// Para ver desde mi aplicacin externa
export function mountVueApp(el) {
  new Vue({
    render: h => h(App),
  }).$mount(el);
}

// Exponer la funcin globalmente
window.mountVueApp = mountVueApp;

