<template>
  <div>
    <p>This section manages important employee compliance documents, including Background Verification (BGV), Drug Test results, and other relevant certifications.</p>

    <button @click="openRequestModal">Request Document</button>
    
    <div class="document-table-wrapper">
      <table class="document-table">
        <thead>
          <tr>
            <th>Name</th>
            <th>Upload Date</th>
            <th>Expiry Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(doc, index) in documents" :key="index">
            <td>{{ doc.name }}</td>
            <td>{{ doc.uploadDate }}</td>
            <td>{{ doc.expiryDate }}</td>
            <td>
              <a :href="doc.url" target="_blank">
                <button class="download-button">Open</button>
              </a>
              <button @click="deleteDocument(index)" class="delete-button">Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    
    <AddDocumentModal
      :isVisible="isModalVisible"
      :employeeId="empleadoData.id"
      @close="closeModal"
      @save="saveDocument"
    />

    <ModalRequestEse
      :isVisible="isRequestModalVisible"
      @close="closeRequestModal"
      @request="handleRequest"
    />
  </div>
</template>

<script>
import AddDocumentModal from './AddDocumentModal.vue';
import ModalRequestEse from './ModalRequestEse.vue';

export default {
  components: {
    AddDocumentModal,
    ModalRequestEse
  },
  props: {
    empleadoData: Object
  },
  data() {
    return {
      isModalVisible: false,
      isRequestModalVisible: false,
      documents: []
    };
  },
  mounted() {
    this.fetchDocuments();
  },
  methods: {
    async fetchDocuments() {
      try {
        const response = await fetch(`your-api-endpoint/documents/${this.empleadoData.id}`);
        if (!response.ok) {
          throw new Error('Unable to fetch documents');
        }
        this.documents = await response.json();
      } catch (error) {
        console.error(error);
        this.documents = this.generateSampleDocuments();
      }
    },
    generateSampleDocuments() {
      return [
        // Sample documents
      ];
    },
    openAddDocumentModal() {
      this.isModalVisible = true;
    },
    openRequestModal() {
      this.isRequestModalVisible = true;
    },
    
    closeRequestModal() {
      this.isRequestModalVisible = false;
    },
    saveDocument(formData) {
      this.$emit('add-document', formData);
      this.closeModal();
    },
    deleteDocument(index) {
      this.$emit('delete-document', index);
    },
    handleRequest() {
      // Handle the request for a new document
      this.closeRequestModal();
    }
  }
};
</script>

<style scoped>
.document-table-wrapper {
  max-height: 60vh;
  overflow-y: auto;
  margin-top: 20px;
}

.document-table {
  width: 100%;
  border-collapse: collapse;
}

.document-table th, .document-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.document-table th {
  background-color: #f2f2f2;
}

.download-button, .delete-button {
  padding: 5px 10px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.download-button {
  background-color: #007bff;
  color: white;
}

.delete-button {
  background-color: #dc3545;
  color: white;
  margin-left: 5px;
}
</style>
