<template>
  <div id="app">
    <Header
      :modules="modules"
      :selectedModule="selectedModule"
      @select-module="handleSelectModule"
    />
    <ContentSection :currentComponent="currentComponent" />
  </div>
</template>

<script>
import Header from './components/EmpleadosHeader.vue';
import ContentSection from './components/ContentSection.vue';
import Module1 from './components/EmpleadosExpediente.vue';
import Module2 from './components/EmpleadosCursos.vue';
import Module3 from './components/EmpleadosEvaluacion.vue';

export default {
  components: {
    Header,
    ContentSection
  },
  data() {
    return {
      modules: [
  { 
    id: 'module1', 
    title: 'Employee File', 
    component: Module1 
  },
  { 
    id: 'module2', 
    title: 'Courses and Training', 
    component: Module2 
  },
  { 
    id: 'module3', 
    title: 'Evaluations', 
    component: Module3 
  }
],
      selectedModule: 'module1'
    };
  },
  computed: {
    currentComponent() {
      const module = this.modules.find(m => m.id === this.selectedModule);
      return module ? module.component : null;
    }
  },
  methods: {
    handleSelectModule(id) {
      this.selectedModule = id;
    }
  }
};
</script>

<style scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  padding: 0;
}
</style>
