<template>
  <div class="profile-tabs">
    <button @click="setTab('general')" :class="{ active: currentTab === 'general' }">General Information</button>
    <button @click="setTab('medical')" :class="{ active: currentTab === 'medical' }">Medical Information</button>
    <button @click="handleDocumentsClick" :class="{ active: currentTab === 'documents' }">Employee Files
      <span v-if="status === 'amarillo' || status === 'rojo'" class="alert-symbol" :class="status"></span>
    </button>
    <button @click="setTab('bgv')" :class="{ active: currentTab === 'bgv' }">BGV & Health Records</button>
  </div>
</template>

<script>
export default {
  props: {
    currentTab: String,
    status: String,
  },
  methods: {
    setTab(tab) {
      this.$emit('update:tab', tab);
    },
    handleDocumentsClick() {
      this.setTab('documents'); // Cambia la pestaa
      this.$nextTick(() => {
        // Elimina el span despus de que se haya procesado el clic
        const span = event.currentTarget.querySelector('.alert-symbol');
        if (span) {
          span.style.display = 'none'; // Oculta el span
        }
      });
    }
  }
};
</script>

<style scoped>
.profile-tabs {
  display: flex;
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
}

.profile-tabs button {
  background: none;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 18px;
  color: #007bff;
  transition: border-bottom 0.3s ease;
}

.profile-tabs button.active {
  border-bottom: 3px solid #007bff;
  font-weight: bold;
}
.alert-symbol {
  display: inline-block;
  width: 10px; /* Ajusta el tamao */
  height: 10px; /* Ajusta el tamao */
  border-radius: 50%;
  margin-left: 5px; /* Espaciado entre el texto y el crculo */
}

.amarillo {
  background-color: rgba(205, 202, 0, 0.915);
}

.rojo {
  background-color: rgba(255, 0, 0, 0.906);
}
</style>
