<template>
  <div class="modal" v-if="isVisible">
    <div class="modal-content">
      <span class="close" @click="close">&times;</span>
      <h2>Request Document</h2>
      <!-- Add your form or input fields here -->
      <button @click="submitRequest">Submit Request</button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isVisible: Boolean
  },
  methods: {
   
    submitRequest() {
      // Handle the document request logic here
      this.$emit('request', { /* request data */ });
      this.close();
    }
  }
};
</script>

<style scoped>
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
}
.close {
  cursor: pointer;
}
</style>