<template>
  <div class="profile-header">
    <div class="profile-photo-section">
      <img :src="imageUrl" alt="Profile Picture" class="profile-picture" @error="setDefaultImage"
        @click="showModal = true">

      <input type="file" id="uploadPhoto" @change="handleFileUpload" style="display: none;">
      <label for="uploadPhoto" class="upload-icon"> Update </label>
    </div>
    <div class="profile-info">
      <h2>{{ localEmpleadoData.nombre }}</h2>
    </div>
    <div class="profile-extra-info">
      <p><strong>ID:</strong> {{ localEmpleadoData.id }}</p>
      <p><strong>Date of Entry:</strong> {{ localEmpleadoData.fecha_creacion }}</p>
    </div>

    <!-- Modal -->
    <div v-if="showModal" class="modal" @click="showModal = false">
      <img :src="imageUrl" alt="Profile Picture" class="modal-image" @click.stop>
      <span class="close" @click="showModal = false">&times;</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    empleadoData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      showModal: false,
      defaultImage: `${this.$imageUrl}perfil.png`,
      localEmpleadoData: this.initializeLocalEmpleadoData(), // Inicializa correctamente
      currentImageName: ''
    };
  },
  computed: {
    imageUrl() {
      return this.localEmpleadoData.foto
        ? `${this.$imageUrl}${this.localEmpleadoData.foto}`
        : this.defaultImage;
    }
  },
  methods: {
    initializeLocalEmpleadoData() {
      return {
        nombre: this.empleadoData.nombre || 'Nombre no disponible',
        id: this.empleadoData.id || 'ID no disponible',
        fecha_creacion: this.empleadoData.fecha_creacion || 'Fecha no disponible',
        foto: this.empleadoData.foto || ''
      };
    },
    setDefaultImage(event) {
      event.target.src = this.defaultImage;
    },
    async handleFileUpload(event) {
      const file = event.target.files[0];
      if (!file) return;

      const formData = new FormData();
      const id = this.localEmpleadoData.id;
      const carpeta = '_perfilEmpleado';

      formData.append('foto', file);
      formData.append('carpeta', carpeta);
      if (this.currentImageName) {
        formData.append('currentImage', this.currentImageName);
      }

      try {
        const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
        const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;

        if (!csrfToken) {
          console.error('CSRF token no encontrado');
          return;
        }

        const response = await fetch(`/api/empleados/${id}/foto`, {
          method: 'POST',
          body: formData,
          headers: {
            'X-CSRF-TOKEN': csrfToken
          }
        });

        if (response.ok) {
          const data = await response.json();
          this.localEmpleadoData.foto = data.ruta; // Actualiza la foto localmente
        } else {
          const errorData = await response.json();
          console.error('Error al cargar la imagen:', errorData);
        }
      } catch (error) {
        console.error('Error en la solicitud:', error);
      }
    },
    async checkImageExists() {
      return new Promise((resolve) => {
        const img = new Image();
        img.src = `${this.$imageUrl}${this.localEmpleadoData.foto}`;

        img.onload = () => {
          resolve(true); // La imagen existe
        };

        img.onerror = () => {
          resolve(false); // La imagen no existe
        };
      });
    }

  },
  async mounted() {
    const exists = await this.checkImageExists();
    if (!exists) {
      console.log('La imagen no existe. Usando imagen predeterminada.');
      this.localEmpleadoData.foto = ''; // Usa la imagen por defecto
    }
  }
};
</script>


/* ProfileHeader.vue */
<style scoped>
.profile-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #ddd;
  background: #f9f9f9;
  border-radius: 8px;
}

.profile-photo-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-picture {
  border-radius: 50%;
  width: 120px;
  height: 120px;
  object-fit: cover;
  margin-bottom: 10px;
}

.upload-icon {
  font-size: 16px;
  cursor: pointer;
  color: #007bff;
  background: #f0f0f0;
  padding: 5px 10px;
  border-radius: 4px;
  text-align: center;
}

.profile-info {
  flex-grow: 1;
  text-align: center;
}

.profile-info h2 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.profile-extra-info {
  text-align: right;
  max-width: 250px;
}

.profile-extra-info p {
  margin: 5px 0;
  font-size: 16px;
  color: #555;
}

.profile-extra-info p strong {
  color: #007bff;
}

.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-image {
  max-width: 90%;
  max-height: 90%;
}

.close {
  position: absolute;
  top: 20px;
  right: 30px;
  color: white;
  font-size: 40px;
  cursor: pointer;
}
</style>