<template>
  <header class="empleados-header">
    <div
      v-for="module in modules"
      :key="module.id"
      class="empleados-header-button-container"
    >
      <button
        :class="{
          'empleados-header-button': true,
          'empleados-header-button-active': selectedModule === module.id
        }"
        @click="selectModule(module.id)"
      >
        {{ module.title }}
      </button>
      <p v-if="selectedModule === module.id" class="empleados-header-description">
        {{ module.description }}
      </p>
    </div>
  </header>
</template>

<script>
export default {
  props: ['modules', 'selectedModule'],
  methods: {
    selectModule(id) {
      this.$emit('select-module', id);
    }
  }
};
</script>

<style scoped>
.empleados-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  margin: 0;
  background-color: #f0f0f0;
}

.empleados-header-button-container {
  text-align: center;
}

.empleados-header-button {
  display: inline-block;
  margin: 0;
  padding: 12px 24px; /* Ajusta el padding si es necesario */
  border: 2px solid #007bff; /* Aumenta el grosor del borde */
  border-radius: 8px; /* Bordes redondeados */
  background-color: #5783b1; /* Color de fondo para el estado no activo */
  color: white;
  cursor: pointer;
  font-size: 18px; /* Ajusta el tamao de la fuente si es necesario */
  font-weight: bold;
  text-align: center;
  transition: all 0.3s ease;
  outline: none;
}

.empleados-header-button-active {
  background-color: #4098f6; /* Color de fondo para el estado activo */
  border-bottom: 3px solid #ffffff; /* Aade una lnea inferior */
}

.empleados-header-button:hover {
  background-color: #4a6ea9; /* Color de fondo al pasar el mouse */
}

.empleados-header-button:focus {
  box-shadow: 0 0 0 3px rgba(64, 152, 246, 0.3); /* Sombra al hacer foco */
}

.empleados-header-description {
  margin-top: 5px;
  color: #333;
}
</style>
